import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { User } from '../../services/api'
import { useAppSelector } from '../../hooks/hooks'
import { 
  getRolePermissions, 
  getAvailableRolesForUser, 
  canDeleteUser
} from '../../utils/roleUtils'
import Header from '../../components/ui/Header'

// Local interface for demo data that includes name
interface DemoUser extends Omit<User, 'firstName' | 'lastName' | 'role'> {
  name: string;
  role: string; // Use string for demo data
}

const UsersPage: React.FC = () => {
  const currentUser = useAppSelector(state => state.auth.user)
  const [showCreateForm, setShowCreateForm] = useState(false)
  const [editingUser, setEditingUser] = useState<DemoUser | null>(null)
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    role: 'user' as 'user' | 'admin' | 'moderator'
  })
  
  // Demo users data
  const [users, setUsers] = useState<DemoUser[]>([
    {
      id: '1',
      name: 'Admin User',
      email: 'admin@demo.com',
      role: 'admin' as string,
      userType: 'FREELANCER' as any,
      isActive: true,
      createdAt: '2024-01-01',
      updatedAt: '2024-01-01'
    },
    {
      id: '2',
      name: 'John Doe',
      email: 'john@example.com',
      role: 'user' as string,
      userType: 'FREELANCER' as any,
      isActive: true,
      createdAt: '2024-01-15',
      updatedAt: '2024-01-15'
    },
    {
      id: '3',
      name: 'Jane Smith',
      email: 'jane@example.com',
      role: 'user' as string,
      userType: 'FREELANCER' as any,
      isActive: true,
      createdAt: '2024-01-14',
      updatedAt: '2024-01-14'
    },
    {
      id: '4',
      name: 'Mike Johnson',
      email: 'mike@example.com',
      role: 'user' as string,
      userType: 'FREELANCER' as any,
      isActive: false,
      createdAt: '2024-01-13',
      updatedAt: '2024-01-13'
    }
  ])
  
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [searchTerm, setSearchTerm] = useState('')
  const [roleFilter, setRoleFilter] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [totalPages, setTotalPages] = useState(1)
  const [isBackendConnected, setIsBackendConnected] = useState(false)

  useEffect(() => {
    fetchUsers()
  }, [searchTerm, roleFilter, currentPage])

  const fetchUsers = async () => {
    try {
      setLoading(true)
      
      // Try to use the user service if backend is available
      const { userService } = await import('../../services/userService')
      const response = await userService.getUsers({
        page: currentPage,
        limit: 20,
        search: searchTerm || undefined,
        role: roleFilter ? roleFilter.toUpperCase() as 'USER' | 'ADMIN' | 'MODERATOR' : undefined
      })
      
      // Convert API users to demo format
      const demoUsers: DemoUser[] = response.users.map(user => ({
        ...user,
        name: `${user.firstName} ${user.lastName}`,
        role: user.role.toLowerCase() as string
      }))
      setUsers(demoUsers)
      setTotalPages(response.meta.totalPages)
      setIsBackendConnected(true)
      setError('')
    } catch (error: any) {
      console.warn('Backend not available, using demo data:', error)
      setIsBackendConnected(false)
      setError('Backend server not running - showing demo data')
      
      // Filter demo data locally
      let filteredUsers = [...users]
      if (searchTerm) {
        filteredUsers = filteredUsers.filter(user => 
          user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          user.email.toLowerCase().includes(searchTerm.toLowerCase())
        );
      }
      if (roleFilter) {
        filteredUsers = filteredUsers.filter(user => user.role.toLowerCase() === roleFilter.toLowerCase());
      }
      setUsers(filteredUsers)
    } finally {
      setLoading(false)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (editingUser) {
      await handleUpdateUser(e)
      return
    }
    
    try {
      if (isBackendConnected) {
        // Use real API
        const { userService } = await import('../../services/userService')
        const newUser = await userService.createUser(formData as any)
        // Convert API user to demo format
        const demoUser: DemoUser = {
          ...newUser,
          name: `${newUser.firstName} ${newUser.lastName}`,
          role: newUser.role.toLowerCase() as string
        }
        setUsers([demoUser, ...users])
      } else {
        // Demo mode - create user locally
        const newUser: DemoUser = {
          id: String(users.length + 1),
          name: `${formData.firstName} ${formData.lastName}`,
          email: formData.email,
          role: formData.role as string,
          isActive: true,
          createdAt: new Date().toISOString().split('T')[0],
          updatedAt: new Date().toISOString().split('T')[0],
          userType: 'FREELANCER' as any
        }
        setUsers([newUser, ...users])
      }
      
      setFormData({ firstName: '', lastName: '', email: '', password: '', role: 'user' })
      setShowCreateForm(false)
      
      if (!isBackendConnected) {
        setError('User created in demo mode only. Start backend to persist data.')
      }
    } catch (error: any) {
      console.error('Create user error:', error)
      setError(error.message || 'Failed to create user')
    }
  }

  const handleEditUser = (user: DemoUser) => {
    setEditingUser(user)
    setShowCreateForm(true)
    setFormData({
      firstName: user.name.split(' ')[0] || '',
      lastName: user.name.split(' ').slice(1).join(' ') || '',
      email: user.email,
      password: '',
      role: user.role as 'user' | 'admin' | 'moderator'
    })
  }

  const handleUpdateUser = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!editingUser) return

    try {
      const updateData: any = {
        firstName: formData.firstName,
        lastName: formData.lastName,
        email: formData.email,
        role: formData.role.toUpperCase() as 'USER' | 'ADMIN' | 'MODERATOR',
        isActive: editingUser.isActive
      }

      // Only include password if it's provided
      if (formData.password.trim()) {
        updateData.password = formData.password
      }

      if (isBackendConnected) {
        // Use real API
        const { userService } = await import('../../services/userService')
        const updatedUser = await userService.updateUser(editingUser.id, updateData)
        // Convert API user to demo format
        const demoUser: DemoUser = {
          ...updatedUser,
          name: `${updatedUser.firstName} ${updatedUser.lastName}`,
          role: updatedUser.role.toLowerCase() as string
        }
        setUsers(users.map(user => user.id === editingUser.id ? demoUser : user))
      } else {
        // Demo mode - update user locally
        const updatedUser: DemoUser = {
          ...editingUser,
          name: `${formData.firstName} ${formData.lastName}`,
          email: formData.email,
          role: formData.role as string,
          updatedAt: new Date().toISOString().split('T')[0]
        }
        setUsers(users.map(user => user.id === editingUser.id ? updatedUser : user))
      }
      
      setFormData({ firstName: '', lastName: '', email: '', password: '', role: 'user' })
      setEditingUser(null)
      setShowCreateForm(false)
      
      if (!isBackendConnected) {
        setError('User updated in demo mode only. Start backend to persist changes.')
      }
    } catch (error: any) {
      console.error('Update user error:', error)
      setError(error.message || 'Failed to update user')
    }
  }

  const handleDeleteUser = async (userId: string) => {
    if (!confirm('Are you sure you want to delete this user?')) {
      return
    }

    try {
      if (isBackendConnected) {
        const { userService } = await import('../../services/userService')
        await userService.deleteUser(userId)
      }
      
      // Remove user from local state
      setUsers(users.filter(user => user.id !== userId))
      
      if (!isBackendConnected) {
        setError('User deleted in demo mode only. Start backend to persist changes.')
      }
    } catch (error: any) {
      console.error('Delete user error:', error)
      setError(error.message || 'Failed to delete user')
    }
  }

  // Check if current user has permission to access this page
  if (!currentUser || !getRolePermissions(currentUser.role).canManageUsers) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">Access Denied</h1>
          <p className="text-gray-600 mb-4">You don't have permission to access user management.</p>
          <Link 
            to="/dashboard" 
            className="text-indigo-600 hover:text-indigo-500 font-medium"
          >
            Return to Dashboard
          </Link>
        </div>
      </div>
    )
  }



  const getStatusBadge = (status: boolean) => {
    const baseClasses = "inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium"
    return status 
      ? `${baseClasses} bg-green-100 text-green-800`
      : `${baseClasses} bg-red-100 text-red-800`
  }

  const getRoleBadge = (role: string) => {
    const baseClasses = "inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium"
    switch (role) {
      case 'admin':
        return `${baseClasses} bg-purple-100 text-purple-800`
      case 'moderator':
        return `${baseClasses} bg-blue-100 text-blue-800`
      default:
        return `${baseClasses} bg-gray-100 text-gray-800`
    }
  }

  const activeUsers = users.filter(user => user.isActive).length
  const adminUsers = users.filter(user => user.role === 'admin').length

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      {/* Page Title */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            <h1 className="text-3xl font-bold text-gray-900">User Management</h1>
            {!isBackendConnected && (
              <span className="ml-3 inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                Demo Mode
              </span>
            )}
          </div>
          <button
            onClick={() => {
              setShowCreateForm(true)
              setEditingUser(null)
              setFormData({ firstName: '', lastName: '', email: '', password: '', role: 'user' })
            }}
            className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors"
          >
            Create New User
          </button>
        </div>
      </div>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-6 sm:px-0">
          
          {/* Backend Status Message */}
          {!isBackendConnected && (
            <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4 mb-6">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-yellow-400" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                  </svg>
                </div>
                <div className="ml-3">
                  <p className="text-sm text-yellow-700">
                    <strong>Demo Mode:</strong> Backend server not running. Changes won't be persisted. Start the backend with <code className="bg-yellow-100 px-1 rounded">npm run dev:simple</code>
                  </p>
                </div>
              </div>
            </div>
          )}

          {/* Error Message */}
          {error && (
            <div className="bg-red-50 border border-red-200 rounded-md p-4 mb-6">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                  </svg>
                </div>
                <div className="ml-3">
                  <p className="text-sm text-red-700">{error}</p>
                  <button 
                    onClick={() => setError('')}
                    className="mt-2 text-sm text-red-600 hover:text-red-800 underline"
                  >
                    Dismiss
                  </button>
                </div>
              </div>
            </div>
          )}
          
          {/* Create User Modal */}
          {showCreateForm && (
            <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg shadow-xl max-w-md w-full mx-4">
                <div className="px-6 py-4 border-b border-gray-200">
                  <h3 className="text-lg font-medium text-gray-900">
                    {editingUser ? 'Edit User' : 'Create New User'}
                  </h3>
                </div>
                <form onSubmit={handleSubmit} className="px-6 py-4">
                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label htmlFor="firstName" className="block text-sm font-medium text-gray-700 mb-1">
                          First Name
                        </label>
                        <input
                          type="text"
                          id="firstName"
                          name="firstName"
                          value={formData.firstName}
                          onChange={handleInputChange}
                          required
                          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                          placeholder="Enter first name"
                        />
                      </div>
                      <div>
                        <label htmlFor="lastName" className="block text-sm font-medium text-gray-700 mb-1">
                          Last Name
                        </label>
                        <input
                          type="text"
                          id="lastName"
                          name="lastName"
                          value={formData.lastName}
                          onChange={handleInputChange}
                          required
                          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                          placeholder="Enter last name"
                        />
                      </div>
                    </div>
                    
                    <div>
                      <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-1">
                        Email Address
                      </label>
                      <input
                        type="email"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleInputChange}
                        required
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                        placeholder="Enter email address"
                      />
                    </div>
                    
                    <div>
                      <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-1">
                        Password {editingUser && '(leave blank to keep current)'}
                      </label>
                      <input
                        type="password"
                        id="password"
                        name="password"
                        value={formData.password}
                        onChange={handleInputChange}
                        required={!editingUser && isBackendConnected}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                        placeholder={editingUser ? "Enter new password (optional)" : (isBackendConnected ? "Enter password" : "Demo password (not required)")}
                      />
                    </div>
                    
                    <div>
                      <label htmlFor="role" className="block text-sm font-medium text-gray-700 mb-1">
                        Role
                      </label>
                      <select
                        id="role"
                        name="role"
                        value={formData.role}
                        onChange={handleInputChange}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      >
                        {currentUser && getAvailableRolesForUser(currentUser.role).map(role => (
                          <option key={role} value={role.toLowerCase()}>
                            {role.charAt(0).toUpperCase() + role.slice(1).toLowerCase()}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  
                  <div className="flex justify-end space-x-3 mt-6">
                    <button
                      type="button"
                      onClick={() => {
                        setShowCreateForm(false)
                        setEditingUser(null)
                        setFormData({ firstName: '', lastName: '', email: '', password: '', role: 'user' })
                      }}
                      className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-md transition-colors"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="px-4 py-2 text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 rounded-md transition-colors"
                    >
                      {editingUser ? 'Update User' : 'Create User'} {!isBackendConnected && '(Demo)'}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}

          {/* Users Statistics */}
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-3 mb-6">
            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="bg-blue-50 p-3 rounded-md">
                      <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197m13.5-9a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z" />
                      </svg>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Total Users</dt>
                      <dd className="text-2xl font-semibold text-gray-900">{users.length}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="bg-green-50 p-3 rounded-md">
                      <svg className="w-6 h-6 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                      </svg>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Active Users</dt>
                      <dd className="text-2xl font-semibold text-gray-900">{activeUsers}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="bg-purple-50 p-3 rounded-md">
                      <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
                      </svg>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Admins</dt>
                      <dd className="text-2xl font-semibold text-gray-900">{adminUsers}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Users Table */}
          <div className="bg-white shadow rounded-lg">
            <div className="px-4 py-5 sm:p-6">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg leading-6 font-medium text-gray-900">All Users</h3>
                <div className="flex space-x-2">
                  <input
                    type="text"
                    placeholder="Search users..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="px-3 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                  />
                  <select 
                    value={roleFilter}
                    onChange={(e) => setRoleFilter(e.target.value)}
                    className="px-3 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                  >
                    <option value="">All Roles</option>
                    <option value="admin">Admin</option>
                    <option value="moderator">Moderator</option>
                    <option value="user">User</option>
                  </select>
                </div>
              </div>
              
              {loading && isBackendConnected ? (
                <div className="text-center py-8">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-600 mx-auto"></div>
                  <p className="mt-2 text-gray-600">Loading users...</p>
                </div>
              ) : (
                <div className="overflow-x-auto">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          User
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Role
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Status
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Created
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Actions
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {users.map((user) => (
                        <tr key={user.id} className="hover:bg-gray-50">
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="flex items-center">
                              <div className="flex-shrink-0 h-10 w-10">
                                <div className="h-10 w-10 rounded-full bg-gray-300 flex items-center justify-center">
                                  <span className="text-sm font-medium text-gray-700">
                                    {user.name.split(' ').map(n => n[0]).join('')}
                                  </span>
                                </div>
                              </div>
                              <div className="ml-4">
                                <div className="text-sm font-medium text-gray-900">{user.name}</div>
                                <div className="text-sm text-gray-500">{user.email}</div>
                              </div>
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className={getRoleBadge(user.role)}>
                              {user.role.charAt(0).toUpperCase() + user.role.slice(1)}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className={getStatusBadge(user.isActive)}>
                              {user.isActive ? 'Active' : 'Inactive'}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {new Date(user.createdAt).toLocaleDateString()}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                            <button 
                              onClick={() => handleEditUser(user)}
                              className="text-indigo-600 hover:text-indigo-900 mr-3"
                            >
                              Edit {!isBackendConnected && '(Demo)'}
                            </button>
                            {currentUser && canDeleteUser(currentUser.role, user.role) && (
                              <button 
                                onClick={() => handleDeleteUser(user.id)}
                                className="text-red-600 hover:text-red-900"
                              >
                                Delete {!isBackendConnected && '(Demo)'}
                              </button>
                            )}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="mt-6 flex items-center justify-between">
                  <button
                    onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                    disabled={currentPage === 1}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Previous
                  </button>
                  
                  <span className="text-sm text-gray-700">
                    Page {currentPage} of {totalPages}
                  </span>
                  
                  <button
                    onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
                    disabled={currentPage === totalPages}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Next
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </main>
    </div>
  )
}

export default UsersPage 